
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
data() {
return {
show: false,
isLoading: true,
hasContent: false,
goods: [],
page: 0,
fetching: true,
noMore: false,
address: '',
}
},
created() {
this.createdDate()
},
mounted() {
window.addEventListener("scroll", this.handleScroll);
},
methods: {
createdDate() {
this.page = 0;
this.fetching = true;
this.noMore = false;
document.documentElement.scrollTop = 0;
document.body.scrollTop = 0;
this.isLoading = true
this.$http.get('/api/waitingPay?page=' + this.page)
.then((res) => {
this.goods = res.data.data
this.isLoading = false
if (this.goods.length) {
this.hasContent = true
}
})
.catch((err) => {
console.log(err)
})
},
handleScroll() {
let that = this
let heightTop = document.documentElement.scrollTop || document.body.scrollTop;

if (heightTop + window.innerHeight >= document.body.offsetHeight) {

if (this.fetching == true) {

this.fetching = false;
this.page++
this.$http.get('/api/waitingPay?page=' + this.page)
.then(function(res) {
if (res.data.data !== null) {
that.goods = that.goods.concat(res.data.data)

that.fetching = true;
} else {
that.noMore = true
}
})
.catch(function(error) {
console.log(error);
});
}
}
},
addressInfo(data) {
this.show = true;
this.address = data
}
},
computed: {

},
components: {
Selector,
PopupPicker,
XAddress,
},
beforeDestroy() {
window.removeEventListener("scroll", this.handleScroll);
}
}




