export default {
  data() {
    return {
      address: [],
    }
  },
  beforeCreate() {
    this.$http.get('/api/address')
      .then((res) => {

        if (res.data.data.length) {
          this.address = res.data.data
        }
      })
      .catch((err) => {
        console.log(err)
      })
  },
  created() {},
  mounted() {},
  methods: {},
  computed: {},
  components: {}
}




